<script>
import Velocity from 'velocity-animate'
import TWEEN from 'tween.js'
export default {
  name: 'Main',
  data () {
    return {
      swiperOption: {
        direction: 'vertical',
        // slidesPerView: 1,
        // setWrapperSize: true,
        mousewheel: false,
        autoHeight: true,
        height: window.innerHeight,
        observeParents: true,
        shortSwipes: true,
        longSwipesRatio: 0.3,
        speed: 500,
        // preloadImages: false,
        on: {
          touchStart: () => {
            this.stopArrow = true
          },
          touchMove: () => {
            this.stopArrow = true
          },
          slideChangeTransitionEnd: () => {
            if (this.swiper.activeIndex === 1) {
              this.showText1 = true
            }
            if (this.swiper.activeIndex === 2) {
              this.showShanghaiText1 = true
            }
            if (this.swiper.activeIndex === 3) {
              if (this.showWuyiText5 === false) {
                this.showWuyiText1 = true
              } else {
                this.showWuyiText5 = false
              }
            }
            if (this.swiper.activeIndex === 4) {
              this.showDuanwu1Text1 = true
            }
            if (this.swiper.activeIndex === 6) {
              this.showFutureText = true
            }
            if (this.swiper.activeIndex === 7) {
              this.showCake = true
            }
          }
          // touchEnd: (swiper) => {
          //   this.stopArrow = false
          //   this.showArrow = false
          //   // alert('你碰了Swiper')
          // }
        }
      },
      showArrow: true,
      stopArrow: false,
      fadeInDuration: 500,
      fadeOutDuration: 500,
      showHeart: false,
      showTitle: false,
      showText1: false,
      showText2: false,
      showText3: false,
      showText4: false,
      showShanghai: true,
      showShanghaiText1: false,
      showShanghaiText2: false,
      showShanghai2Text1: false,
      showShanghai2Text2: false,
      showShanghai2Text3: false,
      showShanghai2Text4: false,
      showShanghai2Text5: false,
      tweenedFilter: {
        saturate: 1
      },
      showWuyiText1: false,
      showWuyiText2: false,
      showWuyiText3: false,
      showWuyiText4: false,
      showWuyiText5: false,
      showDuanwuBg1: true,
      showDuanwu1Text1: false,
      showDuanwu1Text2: false,
      showDuanwu1Text3: false,
      showDuanwu1Text4: false,
      showDuanwu1Text5: false,
      showDuanwu1Text6: false,
      showDuanwu2Text1: false,
      showDuanwu2Text2: false,
      windowHeight: 0,
      quanzhouStyle: {
        width: '100%',
        height: '',
        position: 'relative',
        margin: '18px',
        border: '2px #e67575 dashed',
        'border-radius': '15px'
      },
      showFutureText: false,
      showCake: false
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    },
    tweenedCSSFilter: function () {
      return 'saturate(' + this.tweenedFilter.saturate + '%)'
    }
  },
  mounted () {
    // document.body.removeChild(document.getElementById('appLoading'))

    this.windowHeight = window.innerHeight
    this.quanzhouStyle.height = this.windowHeight - 36 + 'px'
    this.showArrow = false
    this.stopArrow = false
    setTimeout(() => {
      this.showHeart = true
    }, 1000)
  },
  methods: {
    beforeEnter: function (el) {
      el.style.opacity = 0
    },
    enter: function (el, done) {
      var vm = this
      Velocity(el,
        { opacity: 1 },
        {
          duration: this.fadeInDuration,
          complete: function () {
            done()
            if (!vm.stopArrow) vm.showArrow = false
          }
        }
      )
    },
    leave: function (el, done) {
      var vm = this
      Velocity(el,
        { opacity: 0 },
        {
          duration: this.fadeOutDuration,
          complete: function () {
            done()
            vm.showArrow = true
          }
        }
      )
    },
    afterHeart: function (el) {
      this.showTitle = true
    },
    afterText1: function (el) {
      this.showText2 = true
    },
    afterText2: function (el) {
      this.showText3 = true
    },
    afterText3: function (el) {
      this.showText4 = true
    },
    afterText4: function (el) {
      this.tweenFilter()
    },
    afterText5: function (el) {
      this.showShanghaiText2 = true
    },
    afterText6: function (el) {
      setTimeout(() => {
        this.showShanghai = false
      }, 1000)
    },
    tweenFilter: function () {
      function animate () {
        if (TWEEN.update()) {
          requestAnimationFrame(animate)
        }
      }

      new TWEEN.Tween(this.tweenedFilter)
        .to({
          saturate: 100
        }, 3000)
        .start()
      animate()
    },
    afterShanghaiFade: function () {
      this.showShanghai2Text1 = true
    },
    afterShanghai2Text1: function () {
      setTimeout(() => {
        this.showShanghai2Text2 = true
      }, 1000)
    },
    afterShanghai2Text2: function () {
      setTimeout(() => {
        this.showShanghai2Text3 = true
      }, 1000)
    },
    afterShanghai2Text3: function () {
      setTimeout(() => {
        this.showShanghai2Text4 = true
      }, 1000)
    },
    afterShanghai2Text4: function () {
      setTimeout(() => {
        this.showShanghai2Text5 = true
      }, 1000)
    },
    afterShanghai2Text5: function () {
    },
    afterWuyiText1: function () {
      setTimeout(() => {
        this.showWuyiText2 = true
      }, 1000)
    },
    afterWuyiText2: function () {
      setTimeout(() => {
        this.showWuyiText3 = true
      }, 1000)
    },
    afterWuyiText3: function () {
      setTimeout(() => {
        this.showWuyiText4 = true
      }, 1000)
    },
    afterWuyiText4: function () {
      setTimeout(() => {
        this.showWuyiText1 = false
      }, 1000)
      setTimeout(() => {
        this.showWuyiText2 = false
      }, 2000)
      setTimeout(() => {
        this.showWuyiText3 = false
      }, 3000)
      setTimeout(() => {
        this.showWuyiText4 = false
      }, 4000)
      setTimeout(() => {
        this.showWuyiText5 = true
      }, 5000)
    },
    afterDuanwu1Text1: function () {
      setTimeout(() => {
        this.showDuanwu1Text2 = true
      }, 1000)
    },
    afterDuanwu1Text2: function () {
      setTimeout(() => {
        this.showDuanwu1Text3 = true
      }, 1000)
    },
    afterDuanwu1Text3: function () {
      setTimeout(() => {
        this.showDuanwu1Text4 = true
      }, 1000)
    },
    afterDuanwu1Text4: function () {
      setTimeout(() => {
        this.showDuanwu1Text5 = true
      }, 1000)
    },
    afterDuanwu1Text5: function () {
      setTimeout(() => {
        this.showDuanwu1Text6 = true
      }, 1000)
    },
    afterDuanwu1Text6: function () {
      setTimeout(() => {
        this.showDuanwuBg1 = false
      }, 2000)
      setTimeout(() => {
        this.showDuanwu2Text1 = true
      }, 5000)
    },
    afterDuanwu2Text1: function () {
      setTimeout(() => {
        this.showDuanwu2Text2 = true
      }, 1000)
    },
    cakeBeforeEnter: function (el) {
      el.style.opacity = 1
    },
    cakeEnter: function (el, done) {
      Velocity(el,
        {
          opacity: 1,
          rotateZ: ['360deg', 0]
        },
        {
          duration: 6000,
          loop: true,
          complete: done,
          easing: 'linear'
        }
      )
    }
  }
}
</script>

